import { mapState } from "vuex";

export default {
    data() {
        return {
            page: 2,
            list: [],
            info: [],
            time: null,
        }
    },
    computed: {
        ...mapState({
            stepImg: state => state.mallConfig.plugin.step,
            userInfo: state => state.user.info,
        })
    },
    methods: {

        getList() {
            let that = this;
            that.$request({
                url: that.$api.step.activity_log,
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.list = response.data.list;
                    that.info = response.data.info;
                    for(let i = 0;i < list.length;i++) {
                        list[i].start = true;
                        let begin = list[i].activity.begin_at.replace("-","").replace("-","");
                        if (begin < that.data.time) {
                            list[i].start = false;
                        }
                    }
                    that.list.forEach(function(row,index){
                        row.start = true;
                        if(new Date(row.begin_at).getTime() < that.time) {
                            row.start = false;
                        }
                    })
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
    },

    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        that.time = new Date().getTime();
        that.getList();
    }
}
